<template>
  <div id="app">
    <header>
      <div>
        <img src="./assets/logo-white.png" width="167px">
      </div>
    </header>
    <main>
      <div class="main_content_wrap">
        <section class="main_header">
          <div class="main_logo">
            <img src="./assets/logo-black.png" width="167px">
          </div>
          <div>
            <div></div>
            <div></div>
            <div>3.0</div>
            <div style="margin-top:10px">It is not difficult to borrow well and borrow again</div>
            <div>In just one minute, you can create a receipt between you and him (her), which is not tampered with.</div>
            <div>And permanently exist in the NEBULAS chain, based on the 3 chain of NEBULAS.</div>
          </div>
        </section>
        <section class="main_content">
          <div class="create">
            <div class="create_title">
              <img src="./assets/create.png">
              <span> Create IOU</span>
            </div>
            <div class="input_wrap">
              <span></span>
              <input type="text" placeholder="" v-model="addName">
            </div>
            <div class="input_wrap">
              <span></span>
              <input type="text" placeholder="" v-model="addAmt">
            </div>
            <div class="text_wrap">
              <span></span>
              <textarea placeholder="" v-model="addDes"></textarea>
            </div>
            <div class="button_wrap">
              <button @click="addFun">
                <span v-if="isOnload">...</span>
                <span v-else></span>
              </button>
            </div>
          </div>
          <div class="find">
            <div class="create_title">
              <img src="./assets/find.png">
              <span> Query IOU</span>
            </div>
            <div class="input_wrap">
              <span>ID</span>
              <input type="text" placeholder="ID(jhekunfk)" v-model="findId">
            </div>
            <div class="button_wrap white">
              <button @click="getFun('get')"></button>
            </div>
            <div class="find_result" v-if="hasResult">
              <div class="find_title"></div>
              <div class="find_result_content">
                <div class="result_content_amt">
                  <span></span>
                  <span class="right amt_right">{{nowAmt}}</span>
                </div>
                <div class="result_content_id">
                  <span></span>
                  <span class="right ">{{nowName}}</span>
                </div>
                <div class="result_content_des">
                  <span></span>
                  <span class="right des_right">{{nowDes}}</span>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </main>
  </div>
</template>

<script>


export default {
  name: 'app',
  data () {
    return {
      addName: '',
      addAmt: '',
      addDes: '',
      findId: '',
      nowAmt: '',
      nowId: '',
      nowDes: '',
      nowName: '',
      hasResult: false,
      isOnload: false
    }
  },
  mounted() {
    window._this = this;
  },
  methods: {
    addFun() {
      if(this.addName == '' || this.addAmt == '' || this.addDes == '') {
        alert('')
      } else {
        let no = Number(Math.random().toString().substr(3,0) + Date.now()).toString(36)
        let callArgs = '["' + no + '","' + this.addName + '","' + this.addDes + '","' + this.addAmt + '"]'
        this.isOnload = true
        saveFun(callArgs,no,this);
      }
    },
    getFun(no,callback) {
      if(this.findId == '') {
        alert('')
      } else {
        var from = Account.NewAccount().getAddressString(),
          value = "0",
          nonce = "0",
          gas_price = "1000000",
          gas_limit = "2000000",
          callFunction = "get",
          callArgs = "[\"" + this.findId + "\"]",
          contract = {
            "function": callFunction,
            "args": callArgs
          };
        neb.api.call(from, dappAddress, value, nonce, gas_price, gas_limit, contract).then( (resp) => {
          dealResult(resp, this)
          if(callback) {
            callback()
          }
          if(no == 'get') {
            if(resp.result == '' || resp.result == 'null' || resp.result == null) {
              alert("error: id'" + this.findId + "'id")
            }
          }
        })
      }
    }
  }
}

function saveFun(callArgs,no,_this) {
  let to = dappAddress,
    value = "0",
    callFunction = "save";

  window.no = no
  window.serialNumber = nebPay.call(to, value, callFunction, callArgs, {
    listener: cbPush
  });
  
}

function cbPush(resp) {
  window.intervalQuery = setInterval(function () {
    funcIntervalQuery(resp);
  }, 5000);
}

function funcIntervalQuery(resp) {
  window._this.$http.post('https://mainnet.nebulas.io/v1/user/getTransactionReceipt', {
    hash: resp.txhash
  }).then(res => {
    if (res.data.result.status === 1) {
      window._this.findId = window.no
      alert('id' + window.no + '')
      window._this.isOnload = false
      clearInterval(window.intervalQuery)
    }
  })
}

function dealResult(resp, _this) {
  let result = JSON.parse(resp.result)
  if(result != null) {
    _this.nowId = result.no
    _this.nowDes = result.des
    _this.nowAmt = result.amt
    _this.nowName = result.name

    _this.hasResult = true
  } else {
    _this.hasResult = false
  }
}

</script>

<style>
* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

html, body, #app{
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  background-color: rgb(249,249,249);
  height: 100%;
}

header {
  height: 60px;
  background-color: rgb(38,102,194);
}
header div {
  width: 1200px;
  margin: 0 auto;
}

header div img {
  margin-top: 16px;
}

main {
  width: 1200px;
  margin: 0 auto;
  padding: 50px 100px;
  font-size: 14px;
  color: rgb(120,120,120);
}

.main_logo {
  margin-bottom: 15px;
}

.main_content {
  margin-top: 30px;
}

.create {
  float: left;
  width: 400px;
  height: 380px;
}
.create_title {
  height: 56px;
}
.create_title img {
  width: 56px;
  float: left;
}
.create_title span {
  float: left;
  font-size: 20px;
  line-height: 56px;
  margin-left: 5px;
}
.find {
  float: right;
  width: 400px;
  height: 400px;
}

.input_wrap {
  position: relative;
  height: 48px;
  width: 100%;
  font-size: 18px;
  margin-top: 20px;
}
.input_wrap span {
  position: absolute;
  font-weight: bold;
  left: 20px;
  top: 11px;
  color: #404040;
  user-select: none;
}
.input_wrap input {
  height: 48px;
  width: 100%;
  outline: none;
  border: none;
  line-height: 48px;
  font-size: 18px;
  padding-left: 105px;
}

.text_wrap {
  position: relative;
  width: 100%;
  font-size: 18px;
  margin-top: 20px;
}
.text_wrap span {
  position: absolute;
  font-weight: bold;
  left: 20px;
  top: 11px;
  color: #404040;
  user-select: none;
}
.text_wrap textarea {
  height: 126px;
  width: 100%;
  outline: none;
  border: none;
  font-size: 18px;
  padding-left: 20px;
  padding-top: 45px;
}

.button_wrap {
  margin-top: 20px;
}
.button_wrap button{
  font-size: 18px;
  height: 48px;
  width: 100%;
  border-radius: 5px;
  outline: none;
  border: none;
  background-color: rgb(38,102,194);
  color: #fff;
  cursor: pointer;
}
.button_wrap button:hover {
  opacity: .6;
}
.button_wrap.white button {
  border: 1px solid #ddd;
  background-color: #fff;
  color: #404040;
}

.find_result {
  margin-top: 10px;
}
.find_title {
  color: #404040;
  font-size: 18px;
  font-weight: bold;
  padding-left: 5px;
  margin-bottom: 10px;
}
.find_result_content {
  padding: 20px;
  background-color: #fff;
}

.result_content_amt {
  font-size: 18px;
}
.right {
  float: right;
}
.amt_right {
  color: #404040;
  font-weight: bold;
}

.result_content_id {
  margin-top: 40px;
}

.result_content_des {
  margin-top: 15px;
}
.result_content_des::after {
  content: '';
  display: block;
  clear: both;
}
.des_right {
  text-align: right;
  display: inline-block;
  width: 250px;
}
</style>



